@import "global";
@import "reset";

$desktop: 1600px;
$offset: 20px;
$scroll-start: 0;
$scroll-end: calc(-100% - var(50px));
$duration: 60s;

@mixin breakpoint($point) {
  @if $point == desktop {
    @media (min-width: $desktop) {
      @content;
    }
  }
}

.header__wrapper {
  min-height: 100%;
}
.header-parallax {
  min-height: 100vh;
}
.header-parallax__body {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.header-parallax__text-container {
  display: flex;
  flex-direction: column;
  width: max-content;
  margin: 300px 200px;
  font-size: 30px;
  z-index: 5;
  padding: 25px 50px;
  position: relative;
  backdrop-filter: blur(5px);
  background-color: rgba(100, 100, 101, 0.37);
  border-radius: 15px;
  .header-parallax__label {
    display: flex;
    margin: 0 0 10px 0;
    align-items: center;
    white-space: nowrap;
  }
  .header-parallax__label::before {
    flex: 0 0 70px;
    content: "";
    margin: 0 30px 0 0;
    height: 1px;
    background-color: #7d9900;
  }
  .header-parallax__title {
    margin-top: 10px;
    font-size: 60px;
    padding-bottom: 20px;
    padding-right: 36px;
    span {
      font-weight: 900;
      text-transform: uppercase;
      color: #7d9900;
    }
  }
}
.mask {
  height: 60px;
  position: relative;
  overflow: hidden;
  margin-top: $offset;
  font-size: 60px;
  font-weight: 700;
}

.mask span {
  display: block;
  position: absolute;
  width: max-content;
  top: 80px;
  padding-bottom: $offset;
  background-size: 100% 100%;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  background-repeat: no-repeat;
}

.mask span[data-show] {
  transform: translateY(-100%);
  transition: 0.5s transform ease-in-out;
}

.mask span[data-up] {
  transform: translateY(-200%);
  transition: 0.5s transform ease-in-out;
}

.mask span:nth-child(1) {
  background-image: linear-gradient(180deg, #0057b8 20%, #ffd700);
}

.mask span:nth-child(2) {
  background-image: linear-gradient(180deg, #0057b8 20%, #ffd700);
}

.mask span:nth-child(3) {
  background-image: linear-gradient(180deg, #0057b8 20%, #ffd700);
}

.mask span:nth-child(4) {
  background-image: linear-gradient(180deg, #0057b8 20%, #ffd700);
}

._container {
  max-width: $desktop;
  margin: 0px auto;
  padding: 0px 20px;
}

.header-parallax__images {
  position: absolute;
  width: 120%;
  height: 120%;
  top: -10%;
  left: -10%;
}
.images-parallax {
}
.images-parallax__item {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.images-parallax_icons,
.images-parallax_icons-greyscale,
.images-parallax_humans {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
}
.images-parallax_icons {
  background: url(../img/bgSiteBlackWhite.png) top/cover no-repeat;
  opacity: 25%;
}
.images-parallax_icons-greyscale {
  background: url(../img/bgSite.png) top/cover no-repeat;
  z-index: 2;
}
.images-parallax_humans {
  background: url(../img/photo_for_site_horizontalGreyscale.png) top/cover
    no-repeat;
  z-index: 3;
}

.header {
  font-size: 18px;
  position: fixed;
  top: 30px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #fff;
  width: fit-content;
  box-shadow: 0 8px 32px 0 rgba(100, 100, 101, 0.37);
  backdrop-filter: blur(5px);
  background-color: rgba(100, 100, 101, 0.37);
  border: 1px solid rgba(255, 255, 255, 0.18);
  border-radius: 5px;
  z-index: 5;

  &__icons {
    padding: 20px;
  }

  .header__site-language {
    -webkit-appearance: none;
    appearance: none;
    padding-right: 100px;

    option[value="eng"] {
      background: #2a2a2a;
      width: fit-content;
    }
    option[value="ukr"] {
      background: #2a2a2a;
      width: fit-content;
    }
  }
  .header__navigation ul {
    display: flex;
    padding: 20px 100px;
    justify-content: space-around;
    gap: 7rem;
  }

  .header__navigation ul a {
    position: relative;
  }
  .active {
    color: #7d9900;
  }

  .header__navigation ul a::after {
    content: "";
    width: 0;
    height: 3px;
    background: #7d9900;
    position: absolute;
    left: 0;
    bottom: -6px;
    transition: 0.2s ease-in-out;
  }
  .header__navigation ul a:hover::after {
    width: 100%;
  }
}

//   -------------------about---------------------------

.about {
  padding: 80px 0;
  position: relative;
  color: #ababab;
  background: #171717;
  min-height: 70vh;
  z-index: 2;
  &::before {
    content: "";
    pointer-events: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 150px;
    transform: translate(0px, -100%);
    background: linear-gradient(180deg, rgba(11, 29, 38, 0) 5%, #171717 100%);
  }
  &::after {
    content: "";
    pointer-events: none;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 150px;
    transform: translate(0px, 97%);
    background: linear-gradient(0deg, rgba(11, 29, 38, 0) 5%, #171717 100%);
  }
  .about-row {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    z-index: 2;
    margin-top: 50px;
    &__col-1 {
      flex-basis: 35%;
      padding-left: 150px;
      img {
        width: 100%;

        border-radius: 15px;
      }
    }
    &__col-2 {
      padding-right: 150px;
      flex-basis: 60%;
    }
    &__title {
      font-size: 40px;
      text-align: center;
      font-weight: 900;
    }
    &__tabs-nav {
      display: flex;
      gap: 30px;
      justify-content: flex-start;
    }
    &__tabs-link {
      margin-top: 10px;
      position: relative;
      font-size: 20px;
      font-weight: 500;
      cursor: pointer;
    }

    &__tabs-link::after {
      content: "";
      width: 0;
      height: 3px;
      background-color: #7d9900;
      position: absolute;
      left: 0;
      bottom: -8px;
      transition: 0.5s;
    }
    &__tabs-link.active-link::after {
      width: 50%;
    }
    &__tab-item {
      display: none;
    }
    &__tab-item.active-tab {
      padding-top: 20px;
      // max-width: 700px;
      font-size: 18px;
      margin-top: 30px;
      line-height: 1.5;
      display: block;
    }
    &__tab-item.active-tab li {
      padding-bottom: 10px;
      font-weight: 400;
    }

    &__tab-item.active-tab span {
      font-weight: 600;
    }
  }
}

