// -----------------------portfolio -----------------------

.portfolio {
  padding: 80px 0;
  position: relative;
  color: #ababab;
  background: #171717;
  height: 100%;
  z-index: 3;
  width: 100%;
  display: flex;
  justify-content: center;

  &::before {
    content: "";
    pointer-events: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 150px;
    transform: translate(0px, -100%);
    background: linear-gradient(180deg, rgba(11, 29, 38, 0) 5%, #171717 100%);
  }
  &::after {
    content: "";
    pointer-events: none;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 150px;
    transform: translate(0px, 97%);
    background: linear-gradient(0deg, rgba(11, 29, 38, 0) 5%, #171717 100%);
  }
}
.title-portfolio {
  font-size: 40px;
  text-align: center;
  font-weight: 900;
  padding-bottom: 50px;
}
.portfolio__project {
  display: flex;
  flex-wrap: wrap;
  gap: 150px;
  justify-content: space-around;
  padding: 0 100px;
  width: 100%;
}
.card {
  width: 300px;
  margin: 10px;
  background-color: white;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.5);

  &:hover {
    .card__caption {
      top: 50%;
      transform: translateY(-50%);
    }

    .card__image {
      transform: translateY(-10px);
    }

    .card__thumb {
      &::after {
        top: 0;
      }
    }

    .card__snippet {
      margin: 20px 0;
    }
  }

  &__thumb {
    position: relative;
    max-height: 400px;
    overflow: hidden;

    @include breakpoint(desktop) {
      max-height: 500px;
    }

    &::after {
      position: absolute;
      top: 0;
      display: block;
      content: "";
      width: 100%;
      height: 100%;
      background: linear-gradient(
        0deg,
        rgba(0, 0, 0, 0.7) 40%,
        rgba(255, 255, 255, 0) 100%
      );
      transition: 0.3s;

      @include breakpoint(desktop) {
        top: calc(100% - 140px);
      }
    }
  }

  &__image {
    transition: 0.5s ease-in-out;
  }

  &__caption {
    position: absolute;
    top: 50%;
    z-index: 3;
    padding: 0 20px;
    color: white;
    transform: translateY(70%);
    text-align: center;
    transition: 0.3s;

    @include breakpoint(desktop) {
      top: calc(100% - 110px);
      transform: unset;
    }
  }

  &__title {
    display: -webkit-box;
    max-height: 85px;
    overflow: hidden;

    font-size: 23px;
    line-height: 28px;
    text-shadow: 0px 1px 5px black;
    text-overflow: ellipsis;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
  }

  &__snippet {
    display: -webkit-box;
    max-height: 150px;
    margin: 20px 0;
    overflow: hidden;

    font-size: 16px;
    line-height: 20px;
    text-overflow: ellipsis;
    transition: 0.5s ease-in-out;
    -webkit-line-clamp: 5;
    -webkit-box-orient: vertical;

    @include breakpoint(desktop) {
      margin: 60px 0;
    }
  }

  &__button {
    display: inline-block;
    padding: 10px 20px;
    color: white;
    border: 1px solid white;

    font-size: 12px;
    text-transform: uppercase;
    text-decoration: none;
    transition: 0.3s;

    &:hover {
      color: #fff;
      border: 1px solid #7d9900;
      background-color: #7d9900;
    }
  }
}

.disclaimer {
  position: fixed;
  bottom: 0;
  left: 50%;
  z-index: 2;
  box-sizing: border-box;
  width: 100%;
  padding: 20px 10px;
  background-color: white;
  transform: translateX(-50%);

  font-size: 14px;
  text-align: center;

  &__link {
    color: #755d87;
    text-decoration: none;
  }
}

// ---------------------------------------------contact me -----------------------------------------
.contact {
  padding: 180px 0;
  position: relative;
  color: #fff;
  background: #080808;
  height: 100vh;
  z-index: 2;
  &__title {
    font-size: 40px;
    text-align: center;
    font-weight: 900;
  }
}
.contact__container {
  padding-top: 50px;
  display: flex;
}
.container-left {
  flex-basis: 40%;
  h1 {
    font-size: 20px;
    padding-bottom: 20px;
    text-align: center;
  }
  i {
    font-size: 20px;
    margin: 10px;
    color: #7d9900;
  }
  p,
  a {
    // text-align: center;
    font-size: 15px;
  }
  p:hover,
  a:hover {
    color: #7d9900;
    padding-bottom: 5px;
  }
}
.container-right {
  flex-basis: 60%;
  display: flex;

  .social-icons {
    display: flex;
    flex-basis: 50%;
    flex-direction: column;
    padding-top: 40px;
    font-size: 15px;
    justify-items: center;
    
    i {
      font-size: 30px;
      color: #7d9900;
      width: fit-content;
      height: fit-content;
      margin: 10px;
    }
    i:hover {
      font-size: 35px;
      margin: 7.5px;
    }
  }
}
.download-cv {
  flex-basis: 50%;
  position: relative;
}

.image-cv {
  height: 220px;
  box-shadow: 0 5px 10px 0 rgba(236, 233, 233, 0.2);
  &:hover {
    box-shadow: 0 5px 10px 0 rgba(236, 233, 233, 0.5);
  }
}

.download-cv__button {
  position: absolute;

  opacity: 0;
  transition: all 0.3s ease-out;
  background: rgba(27, 26, 26, 0.9);
  height: fit-content;
  width: fit-content;
  left: 9%;
  overflow: hidden;
  top: 50%;

  &:hover {
    color: #fff;
    text-align: center;
    border: 1px solid #7d9900;
    background-color: #7d9900;
  }

  .button-cv {
    display: inline-block;
    padding: 5px 10px;
    color: white;
    border: 1px solid white;
    font-size: 10px;
    text-align: center;
    text-transform: uppercase;
    text-decoration: none;
    transition: 0.3s;
  }
}
.download-cv__image {
  padding-top: 20px;
  backdrop-filter: blur(55px);
  width: fit-content;
}

.download-cv__image:hover .download-cv__button {
  backdrop-filter: blur(0px);

  opacity: 1;
}
